import React from "react";

interface Props
  extends React.DetailedHTMLProps<
    React.SelectHTMLAttributes<HTMLSelectElement>,
    HTMLSelectElement
  > {
    containerProps?: React.DetailedHTMLProps<React.HTMLAttributes<HTMLDivElement>, HTMLDivElement>
  }

const Select: React.FC<Props> = ({ containerProps, children, ...restSelectProps }) => {
  return (
    <div {...containerProps} className={`flex m-4 ${containerProps?.className}`}>
      <select {...restSelectProps} className={`bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 ${restSelectProps.className}`}>
        {children}
      </select>
    </div>
  );
};

export default Select;
